import React from 'react';

import { Routes, Route } from 'react-router-dom';

import Home from '../pages/Home/Home';

function AppRoutes() {
  return (
    <Routes>
      <Route
        exact
        path="/Portafolio"
        element={
          <Home />
        }
      />
      <Route element={<h1>404 Not Found</h1>} />
    </Routes>
  );
}

export default AppRoutes;
